
















import Vue from "vue";
import { Component } from "vue-property-decorator";

@Component
export default class Labels extends Vue {
  // 1 
  // TODO
  get tags() {
    return this.$store.state.tagList;
  }
  created() {
    this.$store.commit("fetchTag");
  }
  createTag() {
    const name = window.prompt("")!;
    if (name === "") {
      window.alert("");
    } else if (name === null) {
      return;
    } else {
      this.$store.commit("createTag", name);
      window.alert("");
    }
  }
}
